import React from "react";
import axios from "axios";

class PaymentForm extends React.Component {
  constructor(props) {
    super(props);

    this.handleChange = this.handleChange.bind(this);
    this.validateForm = this.validateForm.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    this.state = {
      transactionId: Math.floor((Math.random() + 1) * 100000),
      cardType: "",
      cardNumber: 0,
      cardName: "",
      cvv: 0,
      expiryDate: "",
      valid: {},
      error: {}

    };
  }
  componentDidMount(){
    let ticketsCreated = JSON.parse(window.sessionStorage.getItem("ticketsCreated"))
    let busId = window.sessionStorage.getItem("busId")
    let date = window.sessionStorage.getItem("date")
    if(!busId || !date){
      document.getElementById("submitForm").style.backgroundColor = "grey";
      document.getElementById("submitForm").disabled = true;
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    let loggedIn = JSON.parse(window.sessionStorage.getItem("loggedIn"));
    let ticketsCreated = JSON.parse(window.sessionStorage.getItem("ticketsCreated"))
    let busId = window.sessionStorage.getItem("busId")
    let date = window.sessionStorage.getItem("date")

    if (this.validateForm(e) && loggedIn && busId && date) {
      console.log("Form Validation passed and loggedIn and tickets not yet created");
      let obj = {
        busId: window.sessionStorage.getItem("busId"),
        userId: window.sessionStorage.getItem("userId"),
        date: window.sessionStorage.getItem("date"),
        seatNumber: JSON.parse(window.sessionStorage.getItem("seatNumber")),
        forms: JSON.parse(window.sessionStorage.getItem("forms"))
      };
      console.log(obj)
      document.getElementById("submitForm").style.backgroundColor = "grey";
      document.getElementById("submitForm").disabled = true;
      axios
        .post("http://localhost:5000/api/payment/generateTickets", obj)
        .then((res) => {
          window.sessionStorage.setItem('ticketsCreated', true)
          ticketsCreated = true
        })
        .catch((err) => {
          this.setState({ ticketsCreated: false });
        })
        .then(() => {
          document.getElementById("submitForm").style.backgroundColor = "green";
          document.getElementById("submitForm").disabled = false;
          if (ticketsCreated === false) {
            this.setState({
              reason: "error creating tickets please try again",
            });
          } else {
            document.getElementById("submitForm").style.backgroundColor = "grey";
            document.getElementById("submitForm").disabled = true;
            window.sessionStorage.setItem("ticketsCreated", true)
            window.location.replace("http://localhost:3000/viewTickets");
          }
        });
    } else {
      console.log("Form Validation failed and/or login failed");
    }
  }

  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value });
    this.validateForm(e);
  }

  validateForm(e) {
    let name = e.target.name;
    let value = e.target.value;
    let validCopy = this.state.valid;
    let errorCopy = this.state.error;


    switch (name) {
      case "cardType":
        validCopy[name] = Boolean(value);
        validCopy[name]?errorCopy[name]='':errorCopy[name]="Please Select Any CardType"
        break;

      case "cardNumber":
        validCopy[name] = Boolean(value.length === 16 && value > 0);
        validCopy[name]?errorCopy[name]='':errorCopy[name]="Card Number Must Be 16 Digits"
        break;

      case "cardName":
        e.target.value = value.replaceAll(/[^a-zA-Z' ']/g, "");
        validCopy[name] = Boolean(value);
        validCopy[name]?errorCopy[name]='':errorCopy[name]="Please Fill Out This"
        break;

      case "cvv":
        validCopy[name] = Boolean(value.length === 3);
        validCopy[name]?errorCopy[name]='':errorCopy[name]="CVV Number Must Be Of 3 Digits"
        break;

      case "expiryDate":
        validCopy[name] = Boolean(value);
        validCopy[name]?errorCopy[name]='':errorCopy[name]="Please Provide Date"
        break;

      case "payNow":
        validCopy[name] =
          validCopy.cardType &&
          validCopy.cardNumber &&
          validCopy.cardName &&
          validCopy.cvv &&
          validCopy.expiryDate;
    }

    this.setState({ valid: validCopy });
    this.setState({ error: errorCopy });

    // console.log(`${name}: ${validCopy[name]}`)
    // console.log(this.state.valid)

    if (name === "payNow") {
      return validCopy[name];
    } else if (validCopy[name]) {
      console.log("Green");
      e.target.style.border = "3px solid green";
    } else {
      console.log("red");
      e.target.style.border = "3px solid red";
    }
  }

  render() {
    return (
      <div id="paymentFormContainer">
        <div>
          <span>Amount Rs.{window.sessionStorage.getItem("totalAmount")}</span>
          <span>Transaction ID:{this.state.transactionId}</span>
        </div>
        <form
          id="paymentForm"
          onSubmit={this.handleSubmit}
          name="payNow"
          action=""
        >
          <section>
            <span>Card Type</span>
            <label htmlFor="PaymentFormR1">
              <input
                type="radio"
                value="visa"
                id="PaymentFormR1"
                name="cardType"
                required
                onChange={this.handleChange}
              />
              Visa
            </label>
            <label htmlFor="PaymentFormR2">
              <input
                type="radio"
                value="masterCard"
                id="PaymentFormR2"
                name="cardType"
                required
                onChange={this.handleChange}
              />
              MasterCard
            </label>
            <br/>
            <div id='errorBox'><p>{this.state.error.cardType}</p></div>
          </section>
          <section>
            <span>Card Number</span>
            <input
              type="number"
              name="cardNumber"
              required
              onChange={this.handleChange}
            />
            <br/>
            <div id='errorBox'><p>{this.state.error.cardNumber}</p></div>
          </section>
          <section>
            <span>Name on Card</span>
            <input
              type="text"
              name="cardName"
              required
              onChange={this.handleChange}
            />
            <br/>
            <div id='errorBox'><p>{this.state.error.cardName}</p></div>
          </section>
          <section>
            <span>CVV</span>
            <input
              type="number"
              name="cvv"
              required
              onChange={this.handleChange}
            />
            <br/>
            <div id='errorBox'><p>{this.state.error.cvv}</p></div>
          </section>
          <section>
            <span>Expiry Date</span>
            <input
              type="date"
              name="expiryDate"
              required
              onChange={this.handleChange}
            />
            <br/>
            <div id='errorBox'><p>{this.state.error.expiryDate}</p></div>
          </section>
          <section>
            <button id="submitForm" type="submit">
              Pay Now
            </button>
            <button type="reset">Cancel</button>
          </section>
        </form>
      </div>
    );
  }
}

export default PaymentForm;
