import React from 'react'
import SelectBus from './SelectBus'
const axios = require('axios').default




class PlanTrip extends React.Component{
    constructor(props){
        super(props)


        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleChange = this.handleChange.bind(this)
        this.handleReset = this.handleReset.bind(this)
        
        this.state = {
            fromArray: [],
            toArray: [],
            planTrip: false,
            reason: '' ,
            from: '',
            to: '',
            date: '',
            selectBus: false
        }
    }

    handleSubmit(e){
        e.preventDefault()
        if(this.state.from && this.state.to && this.state.date){
            this.setState({selectBus: true})
            this.setState({reason: ""})
        }else{
            this.setState({selectBus: false})
            this.setState({reason: "Please fill all the details"})
        }
        
        // console.log(this.state)    
    }
    handleChange(e){
        this.setState({[e.target.name]: e.target.value})        
        this.setState({selectBus: false})
        if(this.state.reason){
            this.setState({reason: ""})
        }

    }
    handleReset(e){
        this.setState({from: ''})        
        this.setState({to: ''})        
        this.setState({date: ''})        
        this.setState({selectBus: false})        
    }

    componentDidMount(){

        axios.get('http://localhost:5000/api/planTrip')
        .then((res)=>{
            
            if(Boolean(res.data.planTrip)){
                this.setState({fromArray: res.data.from})
                this.setState({toArray: res.data.to})
                this.setState({planTrip: true})

            }
            else{
                this.setState({planTrip: false})
                this.setState({reason: res.data.reason})
            }
            
        })
        .catch((err)=>{
            this.setState({planTrip: false})
            this.setState({reason: "axios error"})
        })

    }

    render(){
        
        const oneDay = 1000*86400
        const start = new Date().getTime()
        const end = start + oneDay*(6)
        const startDate = new Date(start).toISOString().substr(0,10)
        const endDate = new Date(end).toISOString().substr(0,10)
        
        return(
            <>
                <form onSubmit={this.handleSubmit} id="planTripForm" action="">
                    <p>Plan Your Trip</p>
                    <div id="planTrip">
                        <table id="planTripTable">
                            <tr>
                                <th>From</th>
                                <th>To</th>
                                <th>Travel Date</th>
                                <th></th>
                            </tr>
                            <tr>
                                <td>
                                    <select name='from' onChange={this.handleChange}>
                                        <option selected value=''> Select </option>
                                    {
                                        this.state.fromArray.map((city) => {
                                            return <option value={city}> {city} </option>
                                        })
                                    }
                                    </select>
                                </td>
                                <td>
                                    <select name='to' onChange={this.handleChange}>
                                        <option selected value=''> Select </option>
                                    {
                                        this.state.toArray.map((city) => {
                                            return <option value={city}> {city} </option>
                                        })
                                    }
                                    </select> 
                                </td>
                                <td>
                                    <input name='date' onChange={this.handleChange} type="date" min={startDate} max={endDate} />
                                </td>
                                <td>
                                    <button type='submit' >Plan Trip</button>
                                </td>
                            </tr>
                        </table>
                    </div>
                    <button onClick={this.handleReset} type='reset'>Reset</button>
                    <br/>
                    <section id='errorBox'><div>{this.state.reason}</div></section>
                </form>

                {                                
                    this.state.selectBus? < SelectBus from={this.state.from} to={this.state.to} date={this.state.date} />: ''                                      
                }    
            </>                                                            
        )
    }
}

export default PlanTrip