import React from 'react'
import axios from 'axios'

class ViewTickets extends React.Component{
    constructor(props){
        super(props)

        this.state = {
            viewTickets: false,
            ticketsArray: [],
            busesArray: []
        }
    }

    componentDidMount(){
        let obj = {
            userId: window.sessionStorage.getItem('userId')
        }
        console.log(obj)
        axios.post('http://localhost:5000/api/viewTickets', obj)
        .then((res)=>{
            if(Boolean(res.data.viewTickets)){
                this.setState({busesArray: res.data.busesArray})
                this.setState({ticketsArray: res.data.ticketsArray})   
                this.setState({viewTickets: res.data.viewTickets})
                this.setState({reason: ''})
            }
            else{
                this.setState({reason: res.data.reason})
            }    
        })
        .catch((err)=>{
            this.setState({reason: "Axios error"})
        })
    }


    render(){
        let buses = this.state.busesArray
        let tickets = this.state.ticketsArray
        if(!(buses.length && tickets.length)) {return ''}
        console.log(buses)
        console.log(tickets)
        return(

            <div id='viewTicketsContainer'>
                <div id='viewTickets'>
                    {
                        this.state.ticketsArray.map((ticket,index)=>{                           
                            return(
                                <div>
                                    <p>Ticket Id : {ticket._id}</p>
                                    <p>Bus Id : {buses[index]._id}</p>
                                    <table>
                                        
                                        <h1>-----GREEN BUS TRAVELS-----</h1>
                                    <tr>
                                        <th>From</th>
                                        <td>{buses[index].from}</td>
                                    </tr>
                                    <tr>
                                        <th>To</th>
                                        <td>{buses[index].to}</td>
                                    </tr>
                                    <tr>
                                        <th>Date</th>
                                        <td>{ticket.travelDate}</td>
                                    </tr>
                                    <tr>
                                        <th>Departure Time</th>
                                        <td>{buses[index].departure}</td>
                                    </tr>
                                    </table>
                                    <hr/>
                                    <table>
                                        <tr>
                                            <th>SeatNo</th>
                                            <th>Name</th>
                                            <th>Gender</th>
                                            <th>Fare</th>
                                            <th>Service Tax</th>
                                            <th>Amount</th>
                                        </tr>
                                        {
                                            ticket.forms.map((form)=>{
                                                return(
                                                    <tr>
                                                        <td>{form.seatNumber}</td>
                                                        <td>{form.name}</td>
                                                        <td>{form.gender}</td>
                                                        <td>Rs.{buses[index].busFare}</td>
                                                        <td>Rs.{Math.floor(buses[index].busFare * 0.1)}</td>
                                                        <td>Rs.{Math.floor(buses[index].busFare * 1.1)}</td>
                                                    </tr>
                                                )
                                            })
                                        }   
                                        {/* <tr>
                                            <td>db</td>
                                            <td>db</td>
                                            <td>db</td>
                                            <td>Rs.db</td>
                                            <td>Rs.db</td>
                                            <td>Rs.db</td>
                                        </tr> */}
                                    </table>
                                    <hr/>
                                    <h2>Total Amount : Rs.{(Math.floor(buses[index].busFare * 1.1))*ticket.forms.length}</h2>
                                </div>
                            )
                        })
                    }
                    {/* <div>
                        <table>
                        <tr>
                            <th>From</th>
                            <td>db</td>
                        </tr>
                        <tr>
                            <th>To</th>
                            <td>db</td>
                        </tr>
                        <tr>
                            <th>Date</th>
                            <td>db</td>
                        </tr>
                        <tr>
                            <th>Departure Time</th>
                            <td>db</td>
                        </tr>
                        </table>
                        <hr/>
                        <table>
                            <tr>
                                <th>SeatNo</th>
                                <th>Name</th>
                                <th>Gender</th>
                                <th>Fare</th>
                                <th>Service Tax</th>
                                <th>Amount</th>
                            </tr>   
                            <tr>
                                <td>db</td>
                                <td>db</td>
                                <td>db</td>
                                <td>Rs.db</td>
                                <td>Rs.db</td>
                                <td>Rs.db</td>
                            </tr>
                        </table>
                        <hr/>
                        <h2>Total Amount : Rs.</h2>
                    </div> */}
                </div>
            </div>


        )
    }
}

export default ViewTickets