// SEATS AVAILABLE PENDING
import {Link} from 'react-router-dom'
import React from 'react'
import axios from 'axios'

class SelectBus extends React.Component{
    constructor(props){
        super(props)


        this.state = {
            busesArray: [],
            selectBus: false,
            reason: ''
        }
    }
    componentDidMount(){
        console.log(this.props)
        axios.post('http://localhost:5000/api/selectBus', this.props)
        .then((res)=>{
            if(Boolean(res.data.selectBus)){
                this.setState({busesArray: res.data.busesArray})
                this.setState({selectBus: res.data.selectBus})
                this.setState({reason: ''})
                console.log(this.state)
            }
            else{
                this.setState({reason: res.data.reason})
            }    
        })
        .catch((err)=>{
            this.setState({reason: "Axios error"})
            console.log(err)
        })
    }



    render(){
        return(
            <div id='selectBusContainer'>
                <p>Select Bus</p>
                <div id="selectBus">
                    <table id="selectBusTable">                   
                        <tr>
                            <th>Bus Type</th>
                            <th>Departure</th>
                            <th>Arrival</th>
                            <th>Travel Time</th>
                            <th>Date</th>
                            <th>Seats Available</th>
                            <th>Fare</th>
                            <th></th>
                        </tr>
                        
                            {   
                                
                                this.state.busesArray.map((bus) => {
                                    return (
                                        <tr>
                                            <td>{bus.busType}</td>
                                            <td>{bus.departure}</td>
                                            <td>{bus.arrival}</td>
                                            <td>{bus.travelTime}</td>
                                            <td>{this.props.date}</td>
                                            <td>{bus.seatsAvailable}</td>
                                            <td>{bus.busFare}</td>
                                            <td><Link to={{
                                                pathname: '/selectSeats',
                                                bus: {
                                                    id: bus._id,
                                                    date: this.props.date
                                                }
                                            }}><button>View Seats</button></Link></td>
                                        </tr>
                                    )
                                })
                            }
                    </table>
                </div>
            </div>
        )
    }
}

export default SelectBus