import React from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

class SelectSeats extends React.Component{
    constructor(props){
        super(props)
        this.MyBusContainer = this.MyBusContainer.bind(this)
        this.MySummaryContainer = this.MySummaryContainer.bind(this)
        this.createSeats = this.createSeats.bind(this)
        this.handleSelectedSeats = this.handleSelectedSeats.bind(this)
        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
        
        this.state = {
            userId: window.sessionStorage.getItem('userId'),
            // loggedIn: JSON.parse(window.sessionStorage.getItem('loggedIn')),
            busId: this.props.location.bus === undefined ? window.sessionStorage.getItem('busId'): this.props.location.bus.id ,
            date: this.props.location.bus === undefined ? window.sessionStorage.getItem('date'): this.props.location.bus.date ,
            getBusDetails: false,
            busDetails: [],
            selectedSeats: [],
            unavailableSeats: [],
            // ticketsCreated: false,
            reason: '',
            forms: [{seatNumber: 0, name: '',gender: ''}]
        }
    }

    handleSubmit(){
        document.getElementById('submitForm').style.backgroundColor="grey";
        document.getElementById('submitForm').disabled=true;

        let validData = true
        let flag = false
        let valid = true
        console.log(this.state.forms)
        this.state.forms.forEach((form)=>{
            if(form.name==='' || form.gender===''){
                validData = false
                if(this.state.selectedSeats.length === 0){
                    this.setState({reason: 'You Should Select Atleast One Seat Before Making Payment'})
                }else{
                    this.setState({reason: 'Kindly Fill Out All The Details Before Making Payment'})
                }
                
                // console.log(valid)       
            }
            
            this.state.selectedSeats.forEach((seatNumber)=>{
                if(form.seatNumber === seatNumber){
                    flag = true
                }
            })
            if(valid && flag && validData){
                valid = true
            }
            else{
                valid = false
            }
        })   

        if(this.state.forms.length !== this.state.selectedSeats.length){
            valid = false
        }
        if(!(JSON.parse(window.sessionStorage.getItem('loggedIn')) && this.state.getBusDetails) ){
            valid= false    
        }
        console.log('valid: ',valid)
        if(valid){
            // console.log('valid: ',valid)
            window.sessionStorage.setItem('forms', JSON.stringify(this.state.forms))
            window.sessionStorage.setItem('seatNumber', JSON.stringify(this.state.selectedSeats))
            document.getElementById('submitForm').style.backgroundColor="green";
            document.getElementById('submitForm').disabled=false;
            window.location.replace("http://localhost:3000/payment")
        }
        document.getElementById('submitForm').style.backgroundColor="green";
            document.getElementById('submitForm').disabled=false;
    }


    handleChange = (e,num)=>{

        let createNew = true

        this.state.forms.forEach((form, index)=>{
            if(form.seatNumber === num){
                let formsCopy = this.state.forms
                eval("formsCopy[index]." + e.target.name + '=' + 'String(e.target.value)' )
                this.setState({forms: formsCopy})
                console.log('Updated: ',this.state.forms)

                createNew = false
            }
        })

            if(createNew === true){
                let form = {
                    seatNumber: num,
                    name: e.target.name ==='name'? e.target.value: '',
                    gender: e.target.name ==='gender'? e.target.value: ''
                }
                let formsCopy = this.state.forms
                formsCopy.push(form)
                this.setState({forms: formsCopy})
                console.log('newFrom: ',this.state.forms)
            }
            let formsCopyy = this.state.forms
            this.state.forms.forEach((form,formIndex)=>{
                let erase = true               
                this.state.selectedSeats.forEach((seatNumber, seatIndex)=>{
                    if(form.seatNumber === seatNumber){
                        erase = false                       
                    }
                })
                if(erase){
                    formsCopyy.splice(formIndex,1)
                }
            })
            this.setState({forms: formsCopyy})
    }

    componentDidMount(){
        

        if(this.props.location.bus !== undefined){
            window.sessionStorage.setItem('busId',this.props.location.bus.id )
            window.sessionStorage.setItem('date',this.props.location.bus.date )
        }
        const obj = {
            busId: this.state.busId,
            date: this.state.date
        }   
        axios.post('http://localhost:5000/api/selectSeats/getBusDetails', obj)
        .then((res)=>{
            if(Boolean(res.data.getBusDetails)){
                this.setState({getBusDetails: true})
                this.setState({busDetails: res.data.busDetails[0]})
                this.setState({unavailableSeats: res.data.unavailableSeats})  
            }
            else{
                this.setState({reason: res.data.reason})
            }    
        })
        .catch((err)=>{
            this.setState({reason: "Axios error"})
            console.log(err)
        })
        .then(()=>{
            setTimeout(() => {
                let totalSeats = this.state.busDetails.totalSeats
                for(let i=1; i<=totalSeats; i+=1){
                    let arr1 = this.state.unavailableSeats
                    let unavailable = arr1.indexOf(i) > -1
                    if(unavailable){
                        document.getElementById(String(i)).style.backgroundColor="#555";
                        document.getElementById(String(i)).disabled = true;
                    }
                }
                console.log(this.state)
            }, 10);
        })
        
        
    }

    createSeats(){
        let seats = []
        let totalSeats = this.state.busDetails.totalSeats
        for(let i=1;i<=totalSeats;i=i+1){
            seats = [...seats, <button className='seats' id= {String(i)} onClick={this.handleSelectedSeats}>Seat {i}</button>];
        }
            return(<> {seats} </ >)
    }
    handleSelectedSeats(e){
        let loggedIn = JSON.parse(window.sessionStorage.getItem('loggedIn'))
        // skip seats handler if not logged in
        if(!loggedIn){
            this.setState({reason: "Kindly Login Before Selecting Seats"})
            return 1
        }
        this.setState({reason: ""})

        console.log(e.target.name)
        let arr = this.state.selectedSeats
        let value = Number(e.target.id)
        let index = arr.indexOf(value)
        if(index > -1){
            arr.splice(index, 1)
           
        }else{
            arr.push(value)
        }
        this.setState({selectedSeats: arr})
        console.log(arr)
        console.log(this.state.selectedSeats)
        for(let i=1; i<=40; i+=1){

            let selectedSeats = arr.indexOf(i) > -1
            let arr1 = this.state.unavailableSeats
            let unavailableSeats = arr1.indexOf(i) > -1
            if(unavailableSeats){
                continue
            }
            else if(selectedSeats)
                document.getElementById(String(i)).style.backgroundColor="crimson";
            else
                document.getElementById(String(i)).style.backgroundColor="green";
        }          
    }

    MyBusContainer(){
        return(
            <div id="busContainer">
                <div id="bus" >
                    {this.createSeats()}  
                </div>
            </div>
        )
    }

    MySummaryContainer(){
        return(
            <div id="summaryContainer">
                <div id="summary">
                    <h2>Booking Summary</h2>
                    <section>
                        <span>From</span>
                        <span>{this.state.busDetails.from}</span>
                    </section>
                    <section>
                        <span>To</span>
                        <span>{this.state.busDetails.to}</span>
                    </section>
                    <section>
                        <span>Date</span>
                        <span>{this.state.date}</span>
                    </section>
                    <section>
                        <span>Travel Time</span>
                        <span>{this.state.busDetails.travelTime}</span>
                    </section>
                    <section>
                        <span>Bus Type</span>
                        <span>{this.state.busDetails.busType}: {this.state.busDetails.totalSeats}</span>
                    </section>
                    
                    {                     
                        this.state.selectedSeats.map((num)=>{
                            return(
                            <>
                                <section>
                                    <span>Seat No</span>
                                    <span>{num}</span>
                                </section>
                                <form>
                                    <input type="text" onChange={(e)=>this.handleChange(e,num)} name='name' placeholder="Name" />
                                    <label htmlFor={"selectSeatsR"+num}>   
                                        <input type="radio" onChange={(e)=>this.handleChange(e,num)} value='male' id={"selectSeatsR"+num} name="gender"/>
                                        Male
                                    </label>
                                    <label htmlFor={num+"selectSeatsR"+num}>
                                        <input type="radio" onChange={(e)=>this.handleChange(e,num)} value='female' id={num+"selectSeatsR"+num}  name="gender"/>
                                        Female
                                    </label>
                                </form>
                            </>
                            )
                        })
                    }
                    <section>
                        <span>Total Seats</span>
                        <span>{this.state.selectedSeats.length}</span>
                    </section>
                    <section>
                        <span>Fare</span>
                        <span>Rs.{this.state.busDetails.busFare * this.state.selectedSeats.length }</span>
                    </section>
                    <section>
                        <span>Service Tax</span>
                        <span>Rs.{ (this.state.busDetails.busFare * this.state.selectedSeats.length) * 0.1 }</span>
                    </section>
                    <section>
                        <span>Service Charges</span>
                        <span>Rs.{Math.floor((this.state.busDetails.busFare * this.state.selectedSeats.length) * 1.1)  }</span>
                        {window.sessionStorage.setItem('totalAmount', Math.floor((this.state.busDetails.busFare * this.state.selectedSeats.length) * 1.1) )}
                    </section>
                    <button onClick={this.handleSubmit} id="submitForm">Continue to Payment</button>
                </div>
            </div>
        )
    } 


    render(){
        return(
            <>
            <div id="selectSeatsContainer">
                {this.MyBusContainer()}
                {this.MySummaryContainer()} 
            </div>
            <div id='errorBox'>
                <p >{this.state.reason}</p>
            </div>
            
            </ >
            
        )
    }
}

export default SelectSeats