import axios from 'axios'
import React from 'react'
// import { Link } from "react-router-dom";

class SignUpForm extends React.Component {
    constructor(props){
        super(props)



        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)

        this.state = {
            name: "",
            email: '',
            mobileNumber: '',
            password: '',
            dateOfBirth: '',
            gender: '',
            reason: '',
            signup: false
        }
    }
    
    componentDidMount(){
        this.setState({signup: JSON.parse(window.sessionStorage.getItem('signup'))})
    }
    handleChange(e) {
        this.setState({ [e.target.name]: e.target.value })
    }
    handleSubmit(e) {
        e.preventDefault()

        document.getElementById("signupFormButton").disabled = true; 
        const colour = document.getElementById("signupFormButton").style.backgroundColor
        document.getElementById("signupFormButton").style.backgroundColor = 'grey'

        let obj = {
            name: this.state.name,
            email: this.state.email,
            mobileNumber: this.state.mobileNumber,
            password: this.state.password,
            dateOfBirth: this.state.dateOfBirth,
            gender: this.state.gender
        }
        console.log(obj)
        axios.post('http://localhost:5000/api/signup',obj)
        .then((res)=>{
            this.setState({reason: res.data.reason})
            this.setState({signup: res.data.signup})
            window.sessionStorage.setItem('signup', res.data.signup)
            if(res.data.signup){
                setTimeout(()=>{
                    window.history.back()
                },1000)
                
            }
        })
        .catch((err)=>{
            this.setState({reason: 'error sending data'})
        })
        .then(()=>{
            if(!this.state.signup){
                document.getElementById("signupFormButton").disabled = false;
                document.getElementById("signupFormButton").style.backgroundColor = colour
            }
        })
    }

    render(){
        
        if(this.state.signup){
            return(
            <form id="signUpForm" onSubmit={this.handleSubmit}>
                <h2 style={{color: 'green'}}>You have successfully created account! Please login to enjoy the services.</h2>
            </form>
            )
        }else{

            return(
                <form id="signUpForm" onSubmit={this.handleSubmit}>
                <h2>New user? Signup to enjoy services</h2>
                <input type="name" name='name' onChange={this.handleChange} placeholder="Name" />
                <input type="email" name='email' onChange={this.handleChange} placeholder="Email Id" />
                <input type="tel" name='mobileNumber' onChange={this.handleChange} placeholder="Mobile Number" />
                <input type="password" name='password' onChange={this.handleChange} placeholder="Password" />

                <div>
                    Date Of Birth:
                </div>
                <input type="date" name='dateOfBirth' onChange={this.handleChange} />
                <div>
                    <label htmlFor="signUpR1">
                        <input type="radio" id="signUpR1" name="gender" value='male' onChange={this.handleChange} />
                        Male<br/>
                    </label>
                    <label htmlFor="signUpR2">
                        <input type="radio" id='signUpR2' name="gender" value='female' onChange={this.handleChange} />
                        Female
                    </label>                      
                </div>                              
                <button id='signupFormButton' type='submit'>Sign Up</button>
                <h2 style={{color: 'red'}}>{this.state.reason}</h2>
            </form>                             
            )
        }
    }
}

export default SignUpForm
